import React, {memo} from 'react'
import classNames from 'classnames'
import jtiLogo from 'assets/jti_logo.png'
import { translate } from 'react-multi-lang'
import style from './style.module.scss'

function TopNavigation({ t }) {

  const navigationClasses = classNames(
    'navbar',
    style.navigation,
  )

  const burgerClasses = classNames(
    'navbar-burger burger',
    style.burgerIcon,
  )

  const navBrandClasses = classNames(
    'navbar-brand',
    style.navbarBrand,
  )

  const navbarLogo = classNames(
    'navbar-item',
    style.navbarLogo,
  )

  return (
    <nav className={navigationClasses} role="navigation" aria-label="main navigation">
      <div className={navBrandClasses}>
        <button className={burgerClasses} aria-label='menu' aria-expanded='false' data-target='navbarBasicExample'>
          <span aria-hidden="true"></span>
          <span aria-hidden="true"></span>
          <span aria-hidden="true"></span>
        </button>
        <a className={navbarLogo} href="https://bulma.io">
          <img src={jtiLogo} className={style.img} alt='logo' width="55"/>
        </a>
      </div>
      <div className='navbar-menu'>
        <div className='navbar-end'>
          <div className='navbar-item'>
            <div className="buttons">
              <button className="button is-light">
                {t('common.logout')}
              </button>
            </div>
          </div>
        </div>
      </div>
    </nav>      
  )
}

export default translate(memo(TopNavigation))
