import React, { memo } from 'react'
import { translate } from 'react-multi-lang'

import Message from 'components/elements/Message'

function Loading({ t }) {
  const style = {
    height         : 'calc(80vh - 100px)',
    display        : 'flex',
    flexDirection  : 'column',
    justifyContent : 'center',
  }
  return (
    <div style={style}>
      <Message 
        loading 
        text={ t('loading') + '...' } 
      />
    </div>
  )
}

export default memo(
  translate(Loading)
)
