export default function reducer(state, action) {
  switch (action.type) {

    case 'LOADING':
      return {
        ...state,
        loading: {
          ...state.loading,
          [action.payload]: true,
        }, 
      }

    case 'LOADED':
      return {
        ...state,
        loading: {
          ...state.loading,
          [action.payload]: false,
        }, 
      }

    default:
      return state
  }
}
