function parseResponse (response) {
  if (response) {
    if (response.status === 200) {
      return response.data
    }
    if (response.data && response.data.error) {
      return { error: response.data.error.message || response.data.error.code || 'UNKNOW_ERROR' }
    }
    return { error: response.problem || 'UNKNOW_ERROR' }
  }
  return { error: 'NO_RESPONSE' }
}

function normalizeException (e) {
  throw e
}

export {
  parseResponse,
  normalizeException,
}
