import React, { lazy, Suspense } from 'react'
import { Switch, Route } from 'react-router'

import Layout from 'components/Layout'
import PrivateRoute from 'components/PrivateRoute'

import Loading from 'views/Loading'

const Home = lazy(() => import('views/Home'))
const Table = lazy(() => import('views/Table'))
const Login = lazy(() => import('views/Login/Login'))
const ForgotPassword = lazy(() => import('views/Login/ForgotPassword'))
const SetPassword = lazy(() => import('views/Login/SetPassword'))
const LoginEmail = lazy(() => import('views/Login/LoginEmail'))
const Logout = lazy(() => import('views/Login/Logout'))
const Callback = lazy(() => import('views/Login/Callback'))
const NoMatch = lazy(() => import('views/404'))


export default function () {
  return (
    <Suspense fallback={<Loading />}>
      <Switch>
        <Route path="/login/callback" render={() => <Callback />} />
        <Route path="/resetpassword/:user/:token" render={ () => <SetPassword />} />
        <Route path="/login/forgot" render={ () => <ForgotPassword />} />
        <Route path="/login" render={() => <LoginEmail />} />
        <Route path="/loginSSO" render={() => <Login />} />
        <Route path="/logout" render={() => <Logout />} />
        <PrivateRoute path="/" render={
          () => (
            <Layout>
              <Suspense fallback={<Loading />}>
                <Switch>
                  <Route exact path="/" render={() => <Home />} />
                  <Route exact path="/table/:tableName" render={(props) => <Table {...props}/>} />
                  <Route render={() => <NoMatch />} />
                </Switch>
              </Suspense>
            </Layout>
          )
        }/>
      </Switch>
    </Suspense>
  )
}
