import React, {memo} from 'react'
import { NavLink } from 'react-router-dom'
import classNames from 'classnames'
import { withState } from 'recost'
import style from './style.module.scss'

function LeftNavigation({ tables }) {

  const menuClasses = classNames(
    'menu',
    style.navigation,
  )

  return (
    <aside className={menuClasses}>
      {(tables && tables.length && (
        <>
          <p className="menu-label">
            Data
          </p>
          <ul className="menu-list">
            {tables.map(
              (table, index) => (
                <li key={index}>
                  <NavLink to={`/table/${table.name}`} activeClassName='is-active'>{table.verbose}</NavLink>
                </li>
              )
            )}
          </ul>
        </>
      )) || null}
      <p className="menu-label">
        Syncronization
      </p>
      <ul className="menu-list">
        <li>
          <NavLink to={'/uploadSync'} activeClassName='is-active'>Upload new data</NavLink>
        </li>
        <li>
          <NavLink to='/manage' activeClassName='is-active'>Manage Your Team</NavLink>
          <ul>
            <li>
              <NavLink to='/manage/members' activeClassName='is-active'>Members</NavLink>
            </li>
            <li>
              <NavLink to='/manage/plugins' activeClassName='is-active'>Plugins</NavLink>
            </li>
            <li>
              <NavLink to='/manage/addMember' activeClassName='is-active'>Add a member</NavLink>
            </li>
          </ul>
        </li>
      </ul>
      <p className="menu-label">
        Transactions
      </p>
      <ul className="menu-list">
        <li>
          <NavLink to='/transactions/payments' activeClassName='is-active'>Payments</NavLink>
        </li>
        <li>
          <NavLink to='/transactions/transfers' activeClassName='is-active'>Transfers</NavLink>
        </li>
        <li>
          <NavLink to='/transactions/balance' activeClassName='is-active'>Balance</NavLink>
        </li>
      </ul>
    </aside>
  )
}

const mapStateToProps = (state) => ({
  tables: state.tables,
})

export default memo(withState(mapStateToProps)(LeftNavigation))
