export default function reducer(state, action) {
  switch (action.type) {
    case 'FETCH_TABLES_FULFILLED':
      return {
        ...state,
        tables: action.payload,
        error: {
          ...state.error,
          tables: undefined,
        },
      }

    case 'FETCH_TABLES_REJECTED':
      return {
        ...state,
        tables: [],
        error: {
          ...state.error,
          tables: {
            ...action,
          },
        },
      }

    case 'FETCH_TABLE_CONFIG_FULFILLED':
      return {
        ...state,
        tablesConfiguration: {
          ...state.tablesConfiguration,
          [action.payload.tableName]: {
            name: action.payload.tableName,
            configuration: [
              ...action.payload.configuration,
            ],
          },
        },
        error: {
          ...state.error,
          tablesConfiguration: undefined,
        },
      }

    case 'FETCH_TABLE_CONFIG_REJECTED':
      return {
        ...state,
        tablesConfiguration: {
          ...state.tablesConfiguration,
          [action.payload.tableName]: undefined,
        },
        error: {
          ...state.error,
          tablesConfiguration: {
            ...action,
          },
        },
      }

    case 'FETCH_TABLE_DATA_FULFILLED':
      return {
        ...state,
        tablesData: {
          ...state.tablesData,
          [action.payload.tableName]: {
            name: action.payload.tableName,
            data: [
              ...action.payload.data,
            ],
          },
        },
        error: {
          ...state.error,
          tablesConfiguration: undefined,
        },
      }

    case 'FETCH_TABLE_DATA_REJECTED':
      return {
        ...state,
        tablesData: {
          ...state.tablesData,
          [action.payload.tableName]: undefined,
        },
        error: {
          ...state.error,
          tablesData: {
            ...action,
          },
        },
      }

    default:
      return state
  }
}
