import { dispatch } from 'recost'

class Package {
  constructor (api) {
    this.api = api
  }

  getAll (config) {
    dispatch({
      type : 'LOADING',
      payload: 'tables',
    })

    dispatch({
      type: 'FETCH_TABLES_FULFILLED',
      payload: [
        {
          name: 'brand',
          verbose: 'Brands',
        },
        {
          name: 'product',
          verbose: 'Products',
        },
      ],
    })

    dispatch({
      type : 'LOADED',
      payload: 'tables',
    })

    /*
    this.api.get('/tables').then(
      (payload) => {
        dispatch({
          type: 'FETCH_TABLES_FULFILLED',
          payload,
        })
        return payload
      }
    ).catch(
      (err) => {
        dispatch({
          type: 'FETCH_TABLES_REJECTED',
          payload: err,
        })
        return false
      }
    )
    */
  }

  getTableConfig (table, config) {
    dispatch({
      type: 'LOADING',
      payload: `${table}_config`,
    })

    dispatch({
      type: 'FETCH_TABLE_CONFIG_FULFILLED',
      payload: {
        tableName: table,
        configuration: [
          {
            name: 'id',
            verbose: 'id',
            type: 'int(11)',
            allowNull: false,
            editable: false,
            visible: {
              main: true,
              single: true,
            },
          },
          {
            name: 'name',
            verbose: 'name',
            type: 'varchar(255)',
            allowNull: true,
            editable: true,
            visible: {
              main: true,
              single: true,
            },
          },
          {
            name: 'created_at',
            verbose: 'created at',
            type: 'timestamp',
            allowNull: false,
            editable: false,
            visible: {
              main: true,
              single: true,
            },
          },
          {
            name: 'updated_at',
            verbose: 'updated at',
            type: 'timestamp',
            allowNull: false,
            editable: false,
            visible: {
              main: true,
              single: true,
            },
          },
        ],
      },
    })

    dispatch({
      type: 'LOADED',
      payload: `${table}_config`,
    })
  }

  getTableData (table, config) {
    dispatch({
      type: 'LOADING',
      payload: `${table}_data`,
    })

    const now = (new Date()).toUTCString()

    dispatch({
      type: 'FETCH_TABLE_DATA_FULFILLED',
      payload: {
        tableName: table,
        data: [
          {
            id: 1,
            name: 'João Moura',
            created_at: now,
            updated_at: now,
          },
          {
            id: 2,
            name: 'João Leite',
            created_at: now,
            updated_at: now,
          },
          {
            id: 3,
            name: 'Nuno Cardoso',
            created_at: now,
            updated_at: now,
          },
        ],
      },
    })

    dispatch({
      type: 'LOADED',
      payload: `${table}_data`,
    })
  }

}

export default Package
