export default function reducer(state, action) {
  switch (action.type) {

    case 'FETCH_PORTFOLIO_PENDING':
      return {
        ...state,
        loadingPortfolio : true,
        portfolioFetched : false,
        portfolioArchive : action.payload.archive,
        error            : false, 
      }

    case 'FETCH_PORTFOLIO_FULFILLED':
      const portfolio = action.payload
      return {
        ...state,
        loadingPortfolio : false,
        portfolioFetched : true,
        error            : false,
        portfolio        : portfolio,
      }

    case 'FETCH_PORTFOLIO_REJECTED':
      return {
        ...state,
        loadingPortfolio : false,
        portfolioFetched : false,
        error            : {
          type    : 'FETCH_PORTFOLIO',
          payload : action.payload,
        },
      }

    default:
      return state
  }
}
