import axios from 'axios'
import { parseResponse, normalizeException } from './helpers'
import { apiAddress } from 'config/constants'

// Models
import Table from './models/table'
import Portfolio from './models/portfolio'
import User from './models/user'
import Package from './models/package'
import Contact from './models/contact'
import Import from './models/import'

let service

class Api {
  constructor (configuration) {
    
    this.setup(configuration)

    this.table = new Table(this)
    this.portfolio = new Portfolio(this)
    this.user = new User(this)
    this.package = new Package(this)
    this.contact = new Contact(this)
    this.import = new Import(this)
  }

  setup(config = {}) {

    if( !config.headers ) 
      config.headers = {}
    
    config.headers = {
      Accept: 'application/json',
      'Content-Type': 'application/json',
      ...config.headers,
    }

    service = this.service = axios.create({
      baseURL: apiAddress,
      timeout: 10000,
      withCredentials: true,
      ...config,
    })

    service.interceptors.response.use(function (response) {
      return response
    }, function (error) {
      // Do something with response error
      return Promise.reject(error)
    })
  }

  get (endpoint, config) {
    return service.get(endpoint, config).then(
      parseResponse
    ).catch(
      normalizeException
    )
  }

  put (endpoint, data, config) {
    return service.put(endpoint, data, config).then(
      parseResponse
    ).catch(
      normalizeException
    )
  }

  // const PATCH = (endpoint, data) => api.head(endpoint, data).then((response) => parseResponse(response)).catch((e) => normalizeException(e))

  post (endpoint, data, config) {
    return service.post(endpoint, data, config).then(
      parseResponse
    ).catch(
      normalizeException
    )
  }

  delete (endpoint, config) {
    return service.delete(endpoint, config).then(
      parseResponse
    ).catch(
      normalizeException
    )
  }
}

const api = new Api()

export default api
