import { dispatch } from 'recost'

class Package {
  constructor (api) {
    this.api = api
  }

  getRelated (familyId, id, config) {
    return this.api.get(`/families/${familyId}/products/${id}/related`)
  }

  getPackage (familyId, id, config) {
    dispatch({
      type : 'FETCH_PACKAGE_PENDING',
    })

    this.api.get(`/families/${familyId}/products/${id}`).then( (payload) => {
      dispatch({
        type    : 'FETCH_PACKAGE_FULFILLED',
        payload,
      })
      return payload
    }).catch( (err) => {
      dispatch({
        type    : 'FETCH_PACKAGE_REJECTED',
        payload : err,
      })
      return false
    })
  }

}

export default Package
