
class Import {
  constructor (api) {
    this.api = api
  }

  listImports (config) {
    return this.api.get('/imports', config)
  }

  getImport (id, config) {
    return this.api.get(`/imports/${id}`, config)
  }

  uploadFile (file, config) {
    const data = new FormData()
    data.append('file', file, file.name)
    return this.api.post('/imports', data, config)
  }

  runImport (id, products, config) {
    return this.api.put(`/imports/${id}`, products, config)
  }
}

export default Import
