import { dispatch } from 'recost'

class Portfolio {
  constructor (api) {
    this.api = api
  }

  getPortfolio (familyId, archive = false, config) {

    dispatch({
      type: 'FETCH_PORTFOLIO_PENDING',
      payload: { archive },
    })

    this.api.get(`/families/${familyId}`).then(
      (family) => dispatch({
        type	  : 'FETCH_FAMILY_FULFILLED',
        payload : family,
      })
    )
    return this.api.get(`/families/${familyId}/products?archived=${archive}`).then( 
      (items = []) => {
        dispatch({
          type    : 'FETCH_PORTFOLIO_FULFILLED',
          payload : items,
        })
        return items
      }).catch( (err) => {
        dispatch({
          type    : 'FETCH_PORTFOLIO_REJECTED',
          payload : err,
        })
      return false
    })
  }

}

export default Portfolio
