import api from './api'

class Auth {
  constructor() {
    this.email = localStorage.getItem('jti-global-portfolio-email')
    this.admin = localStorage.getItem('jti-global-portfolio-admin')
    if(this.email){
      this.isAuthenticated = true
      // api.setup({
      //   headers : {
      //     'Authorization' : 'Bearer ' + this.token,
      //   },
      // })
    }
    else {
      this.isAuthenticated = false 
    }
  }


  getAuthenticatedUser() {
    if(this.isAuthenticated)
      return {
        email: this.email,
        token : this.token,
        admin: this.admin,
      }
    else
      return null
  }

  async authenticate() {
    return api.user.getUser().then(
      (user) => {
        if(user.email) {
          this.email = user.email
          this.isAuthenticated = true
          localStorage.setItem('jti-global-portfolio-email', user.email);
        }
        else {
          return this.signout().then(
            () => {
              return false
            }
          )
        }
        return true
      }
    ).catch(this.signout)
  }

  async login(email, password) {
    return api.user.login(email, password).then(
      (user) => {
        this.email = user.email
        this.admin = true // TODO: check if user is admin
        this.isAuthenticated = true
        localStorage.setItem('jti-global-portfolio-email', user.email)
        localStorage.setItem('jti-global-portfolio-admin', this.admin)
        return user
      }
    )
  }


  async signout() {
    return api.user.logout().then(() => {
      localStorage.removeItem('jti-global-portfolio-email')
      this.isAuthenticated = false
      this.email = null
      this.username = null
      this.admin = false
      api.setup()
    }) 
  }
}

const auth = new Auth()

export default auth
