export default function reducer(state, action) {
  switch (action.type) {

    // --- FETCH ---------------------------------------------------------------
    case 'FETCH_USER_FULFILLED':
      return {
        ...state,
        user: action.payload,
      }
    case 'FETCH_USER_REJECTED':
      return {
        ...state,
        error : true,
      }

    // --- LOGIN / LOGOUT ------------------------------------------------------
    case 'LOGIN':
      return {
        ...state,
        user     : action.payload,
        auth     : true,
      }
    case 'LOGOUT':
      return {
        ...state,
        auth     : false,
        user     : false,
      }

    default:
      return state
  }
}
