import { dispatch } from 'recost'
import { backendAddress } from 'config/constants'

const authConfig = {
  baseURL: backendAddress,
}

class User {
  constructor (api) {
    this.api = api
  }

  getUser ( config={} ) {
    dispatch({
      type : 'FETCH_USER_PENDING',
    })
    const request = this.api.get('/users/me', config);
    return request.then(payload => {
      dispatch({ 
        type : 'FETCH_USER_FULFILLED', 
        payload,
      })
      return payload
    }).catch(err => {
      dispatch({ 
        type    : 'FETCH_USER_REJECTED', 
        payload : err,
      })
      return err
    })
  }

  login ( identifier, password ) {
    const data = {
      identifier,
      password,
    }
    return this.api.post('/auth/local', data, authConfig).then(
      (user) => {
        dispatch({ 
          type    : 'FETCH_USER_FULFILLED', 
          payload : user,
        })
        return user
      }
    )
  }

  logout () {
    return this.api.get('/logout', authConfig).then(() => {
      dispatch({type:'LOGOUT'})
      return true
    })
  }



  requestForgotPassword(email, config) {
    const data = {
      email,
    }
    return this.api.post('/users/pwreset', data, config)
  }

  verifyForgotPassword(id, token, config) {
    const url = `/users/${id}/pwreset/${token}`
    return this.api.get(url, config)
  }

  useForgotPassword(id, token, password, config) {
    const url = `/users/${id}/pwreset/${token}`
    const data = {
      password,
    }
    return this.api.put(url, data, config)
  }

  changePassword(password, config) {
    const data = {
      password,
    }
    return this.api.put('/users/me', data, config)
  }
}

export default User
